  metaInfo: {
    title: 'Sự tích trái Thơm (quả dứa) - Truyện cổ chọn lọc',
    meta: [
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_94121043_su-tich-trai-thom-qua-dua.jpg' },
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'Sự tích trái Thơm (quả dứa)' },
      { name: 'Description', content: 'Huyền Nương vốn là một cô gái không thích làm các công việc nữ công gia chánh. Chính vì thế lúc mẹ lâm bệnh cô không biết mọi vị trí của vật dụng trong nhà. Người mẹ vì phiền phức đã ước Huyền Nương có nhiều mắc và nói không có cô thì tốt hơn. Không may, điều ước thành sự thật và Huyền Nương bị biến thành một loài cây kỳ lạ có trái với nhiều mắt bao bọc xong quanh mà sau này gọi là cây thơm. ' },

      { itemprop: 'name', content: 'Truyện cổ tích' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1, maximum-scale=1' },
      { name: 'language', content: 'Vietnamese,vn' },
      { name: 'geo.region', content: 'VN' },
      { property: 'article:tag', content: 'Truyện cổ tích, Chuyện cổ tích' },
      { name: 'robots', content: 'index,follow' },
      { name: '', content: '' },
    ]
  },
  components: {
    LeftCom,
    RightCom,
    Modau,
    KetThuc
  },
  methods: {
     denmodau(){
      Clicks.clickmodau();
    },
    denketthuc(){
      Clicks.clickketthuc();
    },
    clicks(vts) {
      this.ht = vts + 1;
      this.ht2 = vts;
      this.per = vts - 1;
      this.next = vts + 1;
      if (this.ht % 2 === 0) {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], false);
        if (this.per > 0) {
          this.arrClassScc[this.per] = this.setClass(this.arrClassScc[this.per], false);
        }
      } else {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], true);
        if (this.next < this.arrClassScc.length) {
          this.arrClassScc[this.next] = this.setClass(this.arrClassScc[this.next], true);
        }
      }
      this.kkk++;
    },
    setClass(sClass, add) {
      var f = 'flipped';
      if (add) {
        if (sClass.indexOf(f) < 0) {
          sClass = sClass + " " + f;
        }
      } else {
        if (sClass.indexOf(" " + f) > 0) {
          sClass = sClass.replace(" " + f, "");
        } else
          if (sClass.indexOf(f) > 0) {
            sClass = sClass.replace(f, "");
          }
      }
      return sClass;
    },
    getCl(vt) {
      return this.arrClassScc[vt];

    },
    getZindex(vt) {
      if (vt % 2 === 0) {
        return "z-index: " + (4 - vt) + ";"
      } else {
        return "";
      }
    }
  },
  created() {
  }
}
</script>

